import React, { useCallback, useMemo, useState } from 'react'
import { Currency, Price, Token } from '@uniswap/sdk-core'
import { useActiveWeb3React } from 'hooks/web3'
import { useAllTokens } from 'hooks/Tokens'
import useUSDCPrice from 'hooks/useUSDCPrice'
import { ExtendedEther } from 'constants/tokens'
import { useCurrencyBalance } from 'state/wallet/hooks'
import { useTokenComparator } from 'components/SearchModal/sorting'
import CurrencyList from 'components/SearchModal/CurrencyList'
import Hero from './components/Hero'
import Investments from './components/Investments'
import WalletAndAllocation from './components/WalletAndAllocation'
import TokenList from './components/WalletAndAllocation/TokenList'
import WalletItem from './components/WalletAndAllocation/WalletItem'

interface ListData {
  currency: Currency
  price: Price<Currency, Token>
  amount: number
}

const Dashboard: React.FC = () => {
  const { account, chainId } = useActiveWeb3React()
  const allTokens = useAllTokens()
  const [invertSearchOrder] = useState<boolean>(false)
  const [worth, setWorth] = useState(0)

  const tokenComparator = useTokenComparator(invertSearchOrder)

  const sortedTokens: Token[] = useMemo(() => {
    console.log("pooh, allTokens = ", typeof allTokens, " tokenComparator = ", tokenComparator);
    return Object.values(allTokens).sort(tokenComparator)
  }, [allTokens, tokenComparator])

  const ether = useMemo(() => chainId && ExtendedEther.onChain(chainId), [chainId])

  const sortedTokensWithETH: Currency[] = useMemo(() => {
    console.log("pooh, sortedTokens = ", sortedTokens.length, " ether = ", ether);
    return ether ? [ether, ...sortedTokens] : sortedTokens
  }, [ether, sortedTokens])

  const handleAddWorth = (tokenWorth: number) => {
    setWorth(worth + tokenWorth)
  }

  const handleCurrencySelect = useCallback(
    (currency: Currency) => {
      
    }, []
  )

  console.log("pooh, sortedTokensWithETH.length = ", sortedTokensWithETH.length);
  return (
    <>
      <Hero />
      <Investments />
      {/* <WalletAndAllocation currencyList={sortedTokensWithETH} changeWorth={handleAddWorth} /> */}
      {/* <TokenList currencies={sortedTokensWithETH} /> */}
      {/* { sortedTokensWithETH.map((currency) => {
        return (<WalletItem currency={currency} />)
      }) } */}
      <TokenList height={300} currencies={sortedTokensWithETH} onCurrencySelect={handleCurrencySelect} />
    </>
  )
}

export default Dashboard
